import React from 'react';
import { Input } from '@chakra-ui/react';

type Props = {
    value: number | '',
    placeholder?: string,
    onChange: (v: number, isValid: boolean) => void;
}

/**
 * Ownership percentage input
 */
const OwnershipPercentageInput = ({ value, placeholder, onChange }: Props) => (
  <Input
    type="number"
    variant="primary"
    value={value}
    data-testid="percentage-input"
    placeholder={placeholder}
    onChange={(e) => {
      if (e.target.value.startsWith('0')) {
        return;
      }
      // eslint-disable-next-line radix
      const v = parseInt(e.target.value);
      if (v > 100) {
        return;
      }

      const isValid = !Number.isNaN(v) && v > 25;
      onChange(v, isValid);
    }}
  />
);

export default OwnershipPercentageInput;
