/* eslint-disable radix */
import React, {
  useCallback, useEffect, useMemo, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Button, FormControl, FormLabel, useStyleConfig, Text, Flex, Switch,
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../../layout/MiniBox';
import css from './YourOwnerShip.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { getAllOwners, updateOwner } from '../../../../store/middlewares/business';
import { apiErrorHandler } from '../../../../utils/general';
import OwnershipPercentageInput from '../../../../components/OwnershipPercentageInput';

/**
 * Kyb Ownership - Component for collecting the Ownership of the current user.
 * @module KYB
 * @returns {JSX.Element}
 */
const YourOwnerShip = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [moreThan25, setMoreThan25] = useState(false);
  const [ownership, updateOwnerShip] = useState<number | ''>('');

  const dispatch = useAppAsyncDispatch();

  const style:any = useStyleConfig('FormControl', {
    variant: 'primary'
  });

  const { owners, selectedBusiness } = useAppSelector((store) => store.business, shallowEqual);

  const isValid = useMemo(() => !moreThan25 || (!Number.isNaN(ownership) && ownership !== '' && ownership >= 25 && ownership <= 100), [ownership, moreThan25]);

  useEffect(() => {
    if (selectedBusiness) {
      dispatch(getAllOwners(selectedBusiness.id));
    }
  }, [selectedBusiness]);

  useEffect(() => {
    if (owners.length !== 0 && parseInt(owners[0].ownership) >= 25) {
      setMoreThan25(true);
      updateOwnerShip(parseInt(owners[0].ownership));
    }
  }, [owners]);

  const submit = useCallback(() => {
    if (owners.length !== 0 && isValid) {
      dispatch(updateOwner(owners[0].id, {
        ownership: `${moreThan25 ? ownership : 0}`
      })).then(() => {
        navigate('/ao/kyb/ownersList');
      }).catch(apiErrorHandler);
    }
  }, [ownership, owners, moreThan25, isValid]);

  return (
    <div className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          varient="box"
          heading={t('your_ownership.title')}
          footer={(
            <Box>
              <Text variant="small" pb="16px">{t('your_ownership.toHelp')}</Text>
              <Button data-testid="submit-btn" disabled={!isValid} width="100%" variant="primary" type="submit">
                {t('your_ownership.ctaText')}
              </Button>
            </Box>
)}
        >
          <Box width="100%">

            <Flex className={css.sendByCardContainer}>
              <Box>
                <Text data-testid="title" variant="large" textAlign="left">{t('your_ownership.yourOwnership')}</Text>
                <Text data-testid="description" variant="regular" pr="100px" className="text-light" textAlign="left">{t('your_ownership.moreThan25')}</Text>
              </Box>
              <Switch
                data-testid="toggle-switch"
                variant="primary"
                id="send-by-email"
                isChecked={moreThan25}
                onChange={() => {
                  setMoreThan25(!moreThan25);
                }}
              />
            </Flex>

            {moreThan25 ? (
              <FormControl variant="primary" style={style}>
                <FormLabel variant="primary">
                  {t('your_ownership.enterPercentage')}
                </FormLabel>
                <OwnershipPercentageInput placeholder="0%" value={ownership} onChange={updateOwnerShip} />
              </FormControl>
            ) : null}

          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default YourOwnerShip;
